.about-me
    position: relative
    min-height: 100vh
    padding-top: 1em
    background-image: url('%=static=%/assets/about-me/lake.png')
    background-size: cover

    &__container
        position: relative
        top: 0
        left: 0
        right: 0
        bottom: 0
        min-height: inherit
        display: flex
        justify-content: center
        align-items: center
        flex-wrap: wrap


    &__form-wrapper
        overflow: hidden
        min-width: 280px
        max-width: 15rem
        border-radius: 0.6em
        margin-bottom: 3em
        max-height: 17em


    &__form-bg
        width: 100%
        height: 100%
        filter: blur(20px)

    &__form-bg-img
        pointer-events: none
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: 0
        display: flex
        justify-content: center
        align-items: center
        background-image: url('%=static=%/assets/about-me/lake.png')
        background-size: cover

    &__form
        pointer-events: auto
        width: 100%
        height: inherit
        max-height: inherit
        display: flex
        flex-direction: column

        h3
            margin-bottom: 0

    &__form-name, &__form-email, &__form-message
        font-size: 0.6em
        position: relative
        z-index: 1
        border-radius: 0.5em
        border: none
        margin: 1em 2em
        padding: 1em

    &__form-message
        max-width: 100%
        resize: none
        height: 10em

    &__form-action
        margin-top: auto
        display: flex
        justify-content: center

    &__form-send
        font-size: 0.6em
        position: relative
        z-index: 1
        flex-grow: 1
        color: #fff
        background: #00C2AB
        border: none
        padding: 1em
        outline: none
        border-radius: 0 0 0 0.6em

    &__form-clear
        font-size: 0.6em
        color: #fff
        position: relative
        z-index: 1
        flex-grow: 1
        border: none
        padding: 1em
        background: #00938E
        outline: none
        border-radius: 0 0 0.6em 0

