.slider
    position: relative
    width: 100%
    height: 100%
    display: flex
    flex-direction: row
    justify-content: center
    flex-wrap: wrap
    align-items: center

    &__overlay
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: 0
        z-index: 1

    &__preview
        order: 1
        position: relative
        display: flex
        justify-content: center
        align-items: center
        margin-top: 0
        max-width: 100%
        color: $sliderBacklight
        height: 50%
        +from(3)
            width: 50%

    &__img
        width: 90%
        height: 90%
        display: flex
        justify-content: center
        align-items: center

        img
            object-fit: contain
            height: inherit
            width: inherit


    &__button-img
        width: 100%
        height: 100%
        display: flex
        justify-content: center
        align-items: center

        img
            object-fit: cover
            height: inherit
            width: inherit

    &__number
        position: absolute
        z-index: 1
        top: 0
        left: 0
        right: 0
        bottom: 0
        margin: auto
        display: flex
        justify-content: center
        align-items: center
        font-size: 10em

    &__description
        order: 2
        display: flex
        flex-direction: column
        justify-content: center
        align-items: center
        width: 100%
        +from(3)
            order: 1
            width: 50%

    &__previous
        +from(3)
            margin-left: auto

    &__previous, &__next
        color: $sliderBacklight
        margin-top: auto
        order: 3
        position: relative
        z-index: 0
        padding: 0
        background: none
        border: none
        overflow: hidden
        width: 50%
        height: 15%
        &:focus
            z-index: 2
        +from(3)
            width: 25%

        button
            height: 100%

    &__title
        display: flex
        justify-content: center
        align-items: center
        +from(4)

        h2
            text-align: center
            margin: 0

        &:before, &:after
            content: ''
            height: 0.3rem
            width: 1rem
            background-color: black
            margin: 0 .5em
            opacity: .3

    &__technology
        display: flex
        flex-wrap: wrap
        justify-content: center
        list-style: none
        width: 100%

        padding: .5em 0
        border-top: 0.1em solid $backlight
        border-bottom: 0.1em solid $backlight
        li
            margin-left: .5em

    &__watch
        width: 10em
        height: 2em
        color: #fff
        display: flex
        justify-content: center
        align-items: center
        background-color: $backlight
        border-radius: 0.3em
        text-decoration: none
