.whoim
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    color: lighten(silver, 20%)
    opacity: .9
    font-size: 0.9em
    text-align: center
    white-space: nowrap
    line-height: 1.5em
    user-select: none
    font-family: $Roboto

    img
        display: inline-block
        border-radius: 50%
        height: 7em
        width: 7em
        margin-bottom: 20px
