.works
    position: relative
    background-color: $works-bg

    &:before, &:after
        content: ''
        position: absolute
        top: -50vh
        z-index: -1
        height: 100vh
        width: 100vw

        backface-visibility: hidden
        transform-style: preserve-3d

    &:before
        background-color: inherit
        transform: skewX(80deg)
        left: -50%

    &:after
        background-color: inherit
        right: -50%
        transform: skewX(-80deg)

