// Common styles of the project

html {
    font-size: 100%;
    font-size:calc(100% + 1.6vmin)
}

.page {
    min-width: 300px;
    min-height: 2000px;
    margin: 0;
    font-family: $Roboto;
}

.page__wrapper {
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: column;
}

* {
    box-sizing: border-box;
}

*:before, *:after {
    box-sizing: inherit;
}
