.mountain-bg
    background-color:
    z-index: 0
    min-height: 300px
    max-height: 1000px
    height: 100vh
    position: relative
    background: linear-gradient(9deg, rgba(216, 219, 209, 0.98) 0%, rgba(176, 199, 194, 0.98) 99%, rgba(172, 197, 191, 0.98) 100%)

    &__overlay
        position: absolute
        z-index: 1
        width: 100%
        height: 100%
        opacity: 1


        &:before, &:after
            content: ''
            position: absolute
            top: 0
            left: 0
            right: 0
            bottom: 0

        &:before
            height: 10vh

        &:after
            box-shadow: inset 0px -200px 250px -100px rgba(0,0,0,0.75)



    &__arrow-down
        position: absolute
        bottom: 5%
        left: 50%
        transform: translate(-50%, -50%)
        z-index: 2
        opacity: .7

    &__parallax
        height: 100%
        position: absolute
        top: 0
        left: 0
        right: 0
        bottom: 0
        z-index: 0

    &__layer img
        display: block
        position: absolute
        bottom: 0

    &__layer
        position: absolute
        width: 1500px
        height: 100%
        left: 50%
        margin-left: -1000px
        bottom: -30%
        transition: all .2s linear
        user-select: none


        &:last-child
            left: 40%
