.headline
    position: relative
    z-index: 1
    display: flex
    align-items: center
    justify-content: center
    background-color: inherit
    margin: 0

    h1
        font-size: 2em


    h2
        font-size: 1.5em

    h3
        font-size: 1em

    &__text
        display: flex
        text-align: center
        align-items: center
        position: relative
        z-index: 1
        color: #373e42
        font-family: BERNIERRegular
        font-weight: 400
        text-transform: uppercase

        span
            display: flex
            justify-content: center
            align-items: center
            &:before, &:after
                content: ''
                display: inline-block
                height: 0.15em
                width: 0.4em
                background-color: black
                opacity: .3
                margin: 0 .4em

    &__icon
        position: absolute
        top: -1em
        left: 0
        right: 0
        bottom: 0
        margin: auto
        width: 80%
        height: 5em
        z-index: 0
        opacity: 0.3

