.footer
    background: #2C4348
    margin: auto
    flex-grow: 1
    color: #fff
    width: 100%

    &__container
        display: flex
        flex-wrap: wrap
        align-items: center
        justify-content: center
        margin: 0.5em

    &__devider
        width: 100%
        margin: 0
        border: 0.05em solid RGBA(255, 255, 255, 0.1)

    &__sitemap
        font-size: 0.7em
        flex-basis: 100%

        +from(4)
            flex-basis: initial
            margin-right: auto

        ul
            list-style: none
            margin: 0
            padding: 0
            text-align: center

            li
                display: inline-block

    &__social
        display: flex
        justify-content: center
        align-items: center
        margin-top: 0.5em
        margin-bottom: 0.5em

    &__social-icon
        display: flex
        justify-content: center
        align-items: center
        width: 2em
        height: 2em
        border-radius: 50%
        margin-left: 0.5em

        &:first-child
            margin-left: 0

        svg
            opacity: 0.3

        &:hover
            background-color: #fff

            svg
                fill: red

    &__word
        font-size: 0.5em
        flex-basis: 100%
        opacity: 0.3
        text-align: center

        +from(5)
            text-align: left
            flex-basis: 50%
            margin-right: auto

    &__copyright
        font-size: 0.5em
        opacity: 0.3
        +from(5)
            text-align: left
