.recall
    display: flex
    font-family: $Roboto
    flex-direction: column
    justify-content: space-between
    position: relative
    text-align: left
    line-height: 1em


    &::before
        font-family: FontAwesome
        content: ''
        opacity: 0.3

    &__comment
        font-size: 0.7em
        font-weight: 500
        opacity: 0.8

        hyphens: auto

    &__author
        display: flex
        justify-content: flex-start
        align-items: center

    &__name
        color: $backlight
        font-size: 0.8em
        font-weight: 700
        margin-left: 1em

    &__avatar
        width: 3em
        height: 3em

        img
            display: block
            height: inherit
            width: inherit
            border-radius: 50% 50%


