.navigation
    position: absolute
    display: flex
    justify-content: flex-start
    width: 100%
    top: 0
    z-index: 1

    &__svg
        margin: 1em 1em
        height: 2em
        width: 2em
        min-width: 35px
        min-height: 35px
        &:hover
            opacity: 1

    &__vk
        opacity: .5
    &__github
        opacity: .5
    &__in
        opacity: .5
    &__hamburger
        margin-left: auto
